.root {
    width: 100%;
    flex: 1;
    overflow: hidden;
    position: relative;
    font-size: 14px;
    line-height: 1.5;
    color: #fff;
}

.content {
    display: flex;
    flex-wrap: wrap;

    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    overflow: scroll;
    overflow: -moz-scrollbars-none;
    -ms-overflow-style: none;
    padding: 8px 0 16px 0;
    -webkit-overflow-scrolling: touch;

    &::-webkit-scrollbar {
        display: none;
        width: 0 !important;
        height: 0 !important;
        opacity: 0;
    }

    &::-webkit-scrollbar-thumb {
        display: none;
        width: 0 !important;
        height: 0 !important;
        opacity: 0;
    }
}

.responsesContainer {
    margin-top: auto;
    display: block;
    width: 100%;
}
