.root {
    display: block;
    width: 100%;
    margin: 14px 0 0 0;

    &:last-child {
        .backButton {
            display: flex;

            &.hidden {
                transition-delay: 0s;
                pointer-events: none;
                opacity: 0;
                transform: translateX(40px) rotate(90deg);
            }
        }
    }

    &:not(:last-child) {
        .avatar {
            pointer-events: auto;
            opacity: 1;
            transform: none;

            &.hidden {
                pointer-events: auto;
                opacity: 1;
                transform: none;
            }
        }

        .backButton {
            transition-delay: 0s;
            pointer-events: none;
            opacity: 0;
            transform: translateX(40px) rotate(90deg);
        }
    }

    &:first-child {
        .backButton {
            transition: none;
        }
    }
}

.simpleResponseRoot {
    display: flex;
    width: 100%;
}

.avatarContainer {
    display: flex;
    flex-direction: column;
    width: 46px;
    padding: 0 8px;
    position: relative;
    z-index: 1;
    overflow: hidden;
}

.avatarAction {
    width: 100%;
    height: 30px;
    margin: auto 0 5px 0;
    border-radius: 50%;
    position: relative;
}

.avatar {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 50%;
    overflow: hidden;
    opacity: 1;
    transform: none;
    transition: transform .6s ease, opacity .6s ease;
    transition-delay: .2s;

    &.hidden {
        transition-delay: 0s;
        pointer-events: none;
        opacity: 0;
        transform: translateX(-40px) rotate(90deg);
    }

    img {
        position: absolute;
        top: 0;
        left: 0;
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}

.backButton {
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    cursor: pointer;
    background-color: #fff;
    border-radius: 50%;
    opacity: 1;
    transform: none;
    transition: transform .6s ease, opacity .6s ease;
    transition-delay: .2s;
    pointer-events: auto;
    box-shadow: 0 0 10px rgba(0,0,0,.18);

    &.hidden {
        transition-delay: 0s;
        pointer-events: none;
        opacity: 0;
        transform: translateX(40px) rotate(90deg);
    }
}

.messagesContainer {
    width: calc(100% - 46px);
    display: block;
    flex: 1;
}

.userMessage {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    padding: 0 16px 0 46px;
    justify-content: flex-end;
    margin: 14px 0;
    animation: revealUserMessage .6s ease forwards;
}

.userMessageBlob {
    display: block;
    padding: 9px 24px 9px 24px;
    border-radius: 20px 20px 2px 20px;
    background-color: #fff;
    text-align: right;
    color: #000;
    box-shadow: 0 0 6px 0 rgba(0, 0, 0, 0.2);
}

.actionsBlock {
    margin: 14px 0 0 0;
    display: flex;
    width: 100%;
}

.backContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 46px;
    padding: 0 8px;

    + .actionsMessages > div {
        padding: 0 21px 0 0;
    }
}

.actionsMessages {
    flex: 1;

    > div {
        padding: 0 8px 0 0;
    }
}

@keyframes revealUserMessage {
    0% {
        opacity: 0;
        transform: translate(60px, 0) scale(.8);
    }
    100% {
        opacity: 1;
        transform: none;
    }
}

@keyframes revealBackButton {
    0% {
        opacity: 0;
        transform: translateX(40px) rotate(90deg);
    }
    100% {
        opacity: 1;
        transform: rotate(0deg);
    }
}
