$color: #fff;

.root {
    margin: 14px 0 0 0;
}

.bubble {
    display: inline-flex;
    padding: 12px 40px 12px 12px;
    border-radius: 20px;
    background-color: #333;
    font-weight: normal;
    font-size: 14px;
    line-height: 16px;
    color: #fff;
    white-space: pre-wrap;
    box-shadow: 0 0 6px 0 rgba(0, 0, 0, 0.2);
    animation: reveal .4s ease forwards;
}

.loader {
    width: 6px;
    height: 6px;
    border-radius: 50%;
    animation: typing 1.2s cubic-bezier(.42,0,.58,1) infinite;
    position: relative;
    margin: 4px 0;
}

@keyframes typing{
    0%, 100%{
        background-color: rgba($color, 1);
        box-shadow: 12px 0 0 0 rgba($color,0.2),
        24px 0 0 0 rgba($color,0.2);
    }
    30%{
        background-color: rgba($color, 0.4);
        box-shadow: 12px 0 0 0 rgba($color,2),
        24px 0 0 0 rgba($color,0.2);
    }
    70%{ background-color: rgba($color, 0.4);
        box-shadow: 12px 0 0 0 rgba($color,0.2),
        24px 0 0 0 rgba($color,1);
    }
}

@keyframes reveal {
    0% {
        opacity: 0;
        transform: translateX(-30px) scale(.3);
    }
    100% {
        opacity: 1;
        transform: none;
    }
}
