.buttonsContainer {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.payButton {
    flex: 1;
    border-radius: 20px;
    background-color: #fff;
    padding: 9px 12px;
    font-weight: 500;
    color: #333;
    text-align: center;
    width: 100%;
    cursor: pointer;
    white-space: nowrap;
    margin: 0 5px;
    box-shadow: 0 0 6px 0 rgba(0, 0, 0, 0.2);
}

.cardInputContainer {
    padding: 12px 16px;
    border-radius: 20px;
    background-color: #fff;
    box-shadow: 0 0 6px 0 rgba(0, 0, 0, 0.2);
}

.cardInput {
    background-color: #fff;
}

.submitButton {
    display: flex;
    width: 100%;
    font-weight: 500;
    text-align: center;
    justify-content: center;
    align-items: center;
    color: #333;
    padding: 9px 16px;
    border-radius: 20px;
    background-color: #fff;
    margin: 8px 0 0 0;
    cursor: pointer;
    box-shadow: 0 0 6px 0 rgba(0, 0, 0, 0.2);

    svg {
        margin: 0 7px 0 0;
    }
}

.error {
    display: block;
    font-size: 14px;
    line-height: 16px;
    text-align: center;
    color: #fff;
    margin: 8px 0;
}

.disabled {
    pointer-events: none;
}

.spinner {
    animation: spinning 1.4s linear infinite;
}

@keyframes spinning {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}
