.root {
    margin: 14px 0 0 0;
    padding: 0 21px 0 0;
    animation: revealBottomLeft .6s ease forwards;

    &:first-child {
        margin: 0;
    }

    &.revealRight {
        animation: revealRight .6s ease forwards;
    }
}

@keyframes revealBottomLeft {
    0% {
        opacity: 0;
        transform: translate(-60px, 0) scale(.8);
    }
    100% {
        opacity: 1;
        transform: none;
    }
}

@keyframes revealRight {
    0% {
        opacity: 0;
        transform: translate(60px, 0);
    }
    100% {
        opacity: 1;
        transform: none;
    }
}
