@import 'vars';

.root {
    padding: 16px;
    background-color: #1f1f1f;
    position: relative;
    z-index: 10;
    display: flex;
    flex-wrap: wrap;
    box-shadow: 0 0 30px 0 rgba(0, 0, 0, 0.4);
}

.resetButton {
    width: 30px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    transition: transform .6s ease;

    &:hover {
        transform: rotate(-180deg) scale(1.05);

        @media all and (max-width: $mobile-breakpoint) {
            transform: none;
        }
    }
}

.headerLink {
    display: flex;
    flex-wrap: wrap;
    width: calc(100% - 30px);
}

.logo {
    width: 30px;
    height: 30px;
    margin: 0 8px 0 0;
    border-radius: 50%;
    overflow: hidden;

    > img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}

.info {
    flex: 1;
    color: #fff;
    user-select: none;
}

.botName {
    font-weight: bold;
    font-size: 16px;
    line-height: 18px;
    margin: 0;
}

.copyright {
    font-size: 12px;
    line-height: 14px;
    opacity: .5;
    margin: 1px 0 0 0;
}
