@import 'vars';

.root {
    font-size: 16px;
    width: 85px;
    height: 119px;
    position: absolute;
    right: -30%;
    bottom: -30%;
    z-index: 2;
    transform: rotate(-60deg);
    animation: fly 20s linear infinite;

    @media all and (max-width: $mobile-breakpoint) {
        display: none;
    }
}

.sceneWrap {
    display: block;
    height: 100%;
    margin: 0 auto;
    padding-top: 2em;
    width: 100%;
    position: relative;
}

.jet-pack {
    display: block;
    font-size: 1rem;
    left: 50%;
    position: absolute;
    text-align: center;
    top: 50%;
    transform: translate(-50%, -50%);
}

.jet-pack:before {
    background: #fff;
    content: '';
    display: inline-block;
    height: 2.25em;
    left: 50%;
    position: absolute;
    top: 50%;
    transform: translate(-50%, 25%);
    width: 6em;
}

.jet-body {
    background: #fff;
    border-radius: 60% 50%;
    display: inline-block;
    height: 8.5em;
    position: relative;
    width: 3.25em;
    z-index: 3;
    border: 1px solid #000;
}
.jet-body:after {
    border-left: .65em solid transparent;
    border-right: .65em solid transparent;
    border-top: 1.75em solid #FA6C00;
    content: "";
    display: inline-block;
    height: .5em;
    left: 50%;
    position: absolute;
    top: 9em;
    -webkit-transform: translate(-50%, 0);
    transform: translate(-50%, 0);
    z-index: 6;
    animation: flame-animation 0.2s linear infinite;
}
.jet-body:before {
    border-left: .5em solid transparent;
    border-right: .5em solid transparent;
    border-bottom: 1.1em solid #fff;
    content: "";
    display: inline-block;
    left: 50%;
    position: absolute;
    top: 8em;
    transform: translate(-50%, 0);
    width: .2em;
    z-index: 10;
}

.jet-wing-top-circle {
    background: #444349;
    border-radius: 50%;
    box-shadow: inset -0.14em -0.2em 0.25em rgba(255, 255, 255, 0.24), inset -0.4em -0.2em 0.25em rgba(255, 255, 255, 0.4), inset -0.4em -0.2em 1em rgba(68, 67, 73, 0.5);
    display: block;
    height: 1.7em;
    left: 50%;
    position: absolute;
    top: 2.5em;
    transform: translate(-50%, 0);
    width: 1.7em;
}
.jet-wing-top-circle:before, .jet-wing-top-circle:after {
    border-bottom: .3em solid transparent;
    border-top: 1.5em solid transparent;
    border-right: 1.8em solid #fff;
    content: "";
    height: 0;
    left: -1.9em;
    position: absolute;
    top: .9em;
    transform: rotate(-8deg);
    width: 0;
}
.jet-wing-top-circle:after {
    border-bottom: .3em solid transparent;
    border-top: 1.5em solid transparent;
    border-left: 1.8em solid #fff;
    border-right: 0;
    left: auto;
    right: -1.9em;
    transform: rotate(8deg);
}

.needle {
    border-left: .5em solid transparent;
    border-right: .5em solid transparent;
    border-bottom: 3.1em solid #fff;
    height: 0;
    left: 50%;
    position: absolute;
    top: -10.25em;
    transform: translate(-50%, 0);
    width: 0;
    z-index: -1;
}

.flag {
    background: rgba(255, 255, 255, 0.95);
    border-radius: 50%;
    height: 2em;
    left: -.05em;
    position: absolute;
    top: 5em;
    -webkit-transform: rotateZ(-25deg);
    transform: rotateZ(-25deg);
    width: 2em;
    animation: round-animation 2s linear infinite alternate-reverse;
}
.flag:before, .flag:after {
    background: rgba(255, 255, 255, 0.95);
    border-radius: 50%;
    content: "";
    height: 1.5em;
    position: absolute;
    right: 2em;
    top: -.5em;
    width: 1.5em;
}
.flag:after {
    right: 0;
    top: -.5em;
}

@keyframes flame-animation {
    100% {
        border-top-width: 1.9em;
        border-left-width: .5em;
    }
}

@keyframes up-down-animation {
    0% {
        box-shadow: inset 0 -1px 2px rgba(255, 255, 255, 0.95), inset 0 -15px 5px rgba(255, 255, 255, 0.9), inset 0 -20px 10px rgba(68, 67, 73, 0.5);
    }
    100% {
        box-shadow: inset 0 -1px 2px #fff, inset 0 -17px 5px rgba(255, 255, 255, 0.95), inset 0 -22px 12px rgba(68, 67, 73, 0.6);
    }
}

@keyframes fly {
    0% {
        transform: translate(0,0) rotate(-55deg);
    }
    100% {
        transform: translate(-130vw, -130vh) rotate(-60deg);
    }
}
