.root {
    display: flex;
    justify-content: space-between;
}

.button {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 40px;
    padding: 0 10px;
    margin: 0 8px 0 0;
    border-radius: 20px;
    background-color: #666;
    box-shadow: 0 0 6px 0 rgba(0, 0, 0, 0.2);

    &.facebook {
        background-color: #3B5997;
    }

    &.twitter {
        background-color: #55ACEE;
    }

    &.whatsapp {
        background-color: #08E675;
    }

    &:last-child {
        margin: 0;
    }
}
