@import 'vars';

.root {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    background-color: #000;
    font-family: 'Comfortaa', sans-serif;
    //background-image: linear-gradient(123deg, #010101, #232323);
    //background-size: 400% 400%;
    //animation: bgGradien 30s ease infinite;

    @media all and (max-width: $mobile-breakpoint) {
        animation: none;
    }
}

.pane {
    position: relative;
    z-index: 2;
    width: 375px;
    max-width: 100%;
    height: 700px;
    max-height: calc(100vh - 40px);
    background-color: rgba(34,34,34,.6);
    box-shadow: 0 2px 30px 0 rgba(0, 0, 0, 0.4);
    border-radius: 10px;
    overflow: hidden;
    margin: auto;
    display: flex;
    flex-direction: column;

    @media all and (max-width: $mobile-breakpoint) {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        border-radius: 0;
        max-height: 100%;
    }
}

@keyframes bgGradien {
    0% {
        background-position: 0 15%
    }
    50% {
        background-position: 100% 86%
    }
    100% {
        background-position: 0 15%
    }
}
