.root {
    display: block;
    width: 100%;
}

.form {
    position: relative;
}

.input {
    display: block;
    width: 100%;
    padding: 12px 56px 12px 16px;
    border-radius: 20px;
    font-size: 16px;
    line-height: 16px;
    outline: none;
    background-color: #fff;
    border: none;
    appearance: none;
    box-shadow: 0 0 6px 0 rgba(0, 0, 0, 0.2);
    font-family: 'Comfortaa', sans-serif;

    ::-webkit-input-placeholder {
        color: rgba(0,0,0,.5);
    }
    ::-moz-placeholder {
        color: rgba(0,0,0,.5);
    }
    :-ms-input-placeholder {
        color: rgba(0,0,0,.5);
    }
    :-moz-placeholder {
        color: rgba(0,0,0,.5);
    }
}

.submit {
    appearance: none;
    box-shadow: none;
    border: none;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 36px;
    height: 36px;
    position: absolute;
    top: 2px;
    right: 2px;
    border-radius: 50%;
    background-color: #222;
    outline: none;
    cursor: pointer;
}

.error {
    margin: 14px 0 0 0;
    animation: revealBottomLeft .6s ease forwards;

    > div {
        width: 100%;
        border-radius: 20px;
        text-align: center;
        justify-content: center;
    }
}

.spinner {
    animation: spinning 1.4s linear infinite;
}

@keyframes revealBottomLeft {
    0% {
        opacity: 0;
        transform: translate(-60px, 0) scale(.8);
    }
    100% {
        opacity: 1;
        transform: none;
    }
}

@keyframes spinning {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}
