@import 'mixins/crop';

.root {
    display: block;
    width: 100%;
    padding: 8px 20px;
    border-radius: 2px 20px 20px 20px;
    background-color: #333;
    color: #fff;
    text-align: center;
    box-shadow: 0 0 6px 0 rgba(0, 0, 0, 0.2);
}

.title {
    display: block;
    font-weight: 500;
    font-size: 14px;
    line-height: 16px;
}

.url {
    display: block;
    font-size: 12px;
    line-height: 14px;
    color: #8E8E8E;
    @include crop();
}
