.root {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    > * {
        width: calc(50% - 4px);
        margin: 0 0 8px 0;

        &:last-of-type {
            margin: 0;

            &:nth-of-type(2n - 1) {
                width: 100%;
            }
        }

        &:only-child {
            width: 100%;
        }

        &:last-child,
        &:nth-last-of-type(-n+2):not(:nth-child(even)) {
            margin: 0;
        }
    }
}
