.root {
    margin: 0 0 0 -46px;
    white-space: nowrap;
    width: calc(100% + 67px);
    overflow: auto;
    overflow: -moz-scrollbars-none;
    -ms-overflow-style: none;
    padding: 0 0 0 46px;
    display: flex;
    flex-wrap: nowrap;

    &::-webkit-scrollbar {
        display: none;
        width: 0 !important;
        height: 0 !important;
        opacity: 0;
    }

    &::-webkit-scrollbar-thumb {
        display: none;
        width: 0 !important;
        height: 0 !important;
        opacity: 0;
    }
}

.product {
    display: block;
    flex: 0 0 auto;
    vertical-align: top;
    margin: 0 14px 0 0;
    width: 130px;
    border-radius: 10px;
    overflow: hidden;
    box-shadow: 0 0 6px 0 rgba(0, 0, 0, 0.2);

    > div {
        height: 100%;
    }
}
